import classNames from 'classnames'
import { useEffect, useState } from 'react'
import styles from './index.module.scss'
import { Col, Row, Statistic, Divider, List, Typography } from 'antd'
import { visitNum } from '../../../../api/user'
import { Visit_obj } from '../../../../api/APItype'
const Workbench = () => {
  const [count, setCount] = useState(0)
  const [details, setDetails] = useState<Visit_obj[]>()
  useEffect(() => {
    visitNum().then(
      ({
        data: {
          data: { details, count },
          code,
        },
      }) => {
        if (code === 0) {
          details.sort(
            (a, b) => Date.parse(b.loginTime) - Date.parse(a.loginTime)
          )
          setDetails(details)
          setCount(count)
        }
      }
    )
  }, [])
  return (
    <div className={classNames(styles.root)}>
      <Row gutter={16}>
        <Col span={12}>
          <Statistic title="" value={count} />
        </Col>
      </Row>
      <Divider orientation="left"></Divider>
      <List
        bordered
        dataSource={details}
        renderItem={(detail) => (
          <List.Item>
            <Typography.Text mark>[ITEM]</Typography.Text>
            {detail.username}--
            {detail.loginTime}--
            {detail.logoutTime || ''}
          </List.Item>
        )}
      />
    </div>
  )
}

export default Workbench
